import fs from "fs/promises"
import chalk, { ChalkInstance } from "chalk"
import { createHash } from "node:crypto"

import { RuntimeConfig } from "../config"
import { closeDatabase, DbConn, getDatabase, findScriptDataByHash, findTestSpecByScriptDataId } from "../lib/db"
import { cacheFiles } from "../cache"
import { getTargetFiles } from "../lib/utils"
import { Call, Definition, TestScript, TestSpec, TestDefType } from "../lib/types"
import { getTestRunnerCall } from "./calls"
import { closeBrowser, createBrowser } from "./browser"
import { logging } from "selenium-webdriver"
import { TestContext, TestDefinitionRun, TestRun, TestRunType, TestSpecRun, TestStepRun } from "./types"
import { formatNumber, aggregateOutcome, outcomeEnd, outcomeStart, uploadTestResult } from "./utils"
import { format } from "date-fns"
import { renderTestResult } from "./log"
import axios from "axios"

export async function TestRunner(runType: TestRunType, cfg: RuntimeConfig) {
  const run: TestRun = { ...outcomeStart(), projectId: cfg.projectId, runType: runType, specs: [] }
  console.log(chalk.blueBright(`üöÄ Starting test run (id:${run.executionId})`))

  try {
    if (cfg.screenshots) {
      fs.mkdir(`${cfg.screenshotsDir}/${run.executionId}`, { recursive: true })
    }

    switch (runType) {
      case "local":
        await runLocal(run, cfg)
        break
      case "remote":
        await runRemote(run, cfg)
        break
    }

    const res = aggregateOutcome(run, run.specs)

    let fn: ChalkInstance | null = null
    switch (res.result) {
      case "pass":
      case "skip":
        fn = chalk.greenBright
        break
      case "warn":
        fn = chalk.yellowBright
        break
      case "fail":
      case "error":
        fn = chalk.redBright
        break
    }

    await fs.writeFile(
      `${cfg.logsDir}/${format(run.startDate, "yyyyMMddHHmmss")}_${run.executionId}.json`,
      JSON.stringify(run, null, 2),
    )

    await fs.writeFile(
      `${cfg.logsDir}/${format(run.startDate, "yyyyMMddHHmmss")}_${run.executionId}.html`,
      renderTestResult(run),
    )

    await uploadTestResult(cfg, run)

    console.log(fn(`üí• Test run complete: ${res.result}`))
    console.log(fn(`  ${formatNumber(res.passCount)} passed`))
    console.log(fn(`  ${formatNumber(res.failCount)} failed`))
    console.log(fn(`  ${formatNumber(res.skipCount)} skipped`))
    console.log(fn(`  ${formatNumber(res.warnCount)} warnings`))
    console.log(fn(`  ${formatNumber(res.errorCount)} errors`))

    return res
  } catch (e) {
    console.log(chalk.redBright(`ü§ï Exception running tests: ${e}`))
    throw e
  } finally {
    closeDatabase()
  }
}

async function runLocal(run: TestRun, cfg: RuntimeConfig) {
  const db = getDatabase(cfg.cacheDir)

  const testFiles = await getTargetFiles(cfg)

  await cacheFiles(testFiles, cfg, db)

  const specs: TestSpec[] = []
  for (const f of testFiles) {
    const spec = await findTestSpec(f, db)
    specs.push(spec)
    for (const d of spec.definitions) {
      if (d.type === TestDefType.Procedure) {
        cfg.procedures.push(d)
      }
    }
  }

  for (const spec of specs) {
    let countOfTests = 0
    for (const d of spec.definitions) {
      if (d.type === TestDefType.Test) {
        countOfTests++
      }
    }

    if (countOfTests === 0) {
      continue
    }

    const browser = await createBrowser(cfg)

    const ctx: TestContext = {
      testRunId: run.executionId,
      config: cfg,
      browser: browser,
      metadata: {},
      timeout: 10000,
    }

    const specResult = await runSpecTests(spec, ctx)
    run.specs.push(specResult)
  }
}

async function runRemote(run: TestRun, cfg: RuntimeConfig) {
  try {
    if (cfg.taskId) {
      run.taskId = cfg.taskId
    }

    const { status, data: scripts } = await axios.get<TestScript[]>(
      `${cfg.apiBaseURL}/projects/${cfg.projectId}/scripts`,
      {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
          Authorization: `Bearer ${cfg.apiKey}`,
        },
      },
    )
    if (status !== 200) {
      throw `Server returned ${status}`
    }

    for (const s of scripts) {
      if (cfg.testScriptDataId && s.scriptData.id !== cfg.testScriptDataId) {
        continue
      }
      const specResult = await runRemoteScript(s, cfg, run.executionId)
      run.specs.push(specResult)
    }
  } catch (e) {
    console.log(chalk.redBright(`ü§ï Running scripts for project ${cfg.projectId}: ${e}`))
    throw e
  }
}

async function findTestSpec(file: string, db: DbConn) {
  console.log(chalk.whiteBright(`Loading file: ${file}`))

  const data = await fs.readFile(file, { encoding: "utf-8" })

  const hash = createHash("sha256").update(data).digest("base64")
  const existingScript = await findScriptDataByHash(hash, db)
  if (!existingScript) {
    throw "Script not found"
  }

  const spec = await findTestSpecByScriptDataId(existingScript.id, db)
  if (!spec) {
    throw "Test spec not found"
  }

  if (spec.definitions.length === 0) {
    throw "No tests found"
  }

  return spec
}

export async function runSpecTests(spec: TestSpec, ctx: TestContext) {
  if (spec.name) {
    console.log(chalk.whiteBright(spec.name))
    console.log(chalk.whiteBright("=".repeat(spec.name.length)))
  }

  try {
    const specResult: TestSpecRun = { ...outcomeStart(spec.name), specId: spec.id, tests: [] }

    for (const d of spec.definitions) {
      if (d.type !== TestDefType.Test) {
        continue
      }
      if (!ctx.config.test || d.name === ctx.config.test) {
        const testResult = await runTest(d, ctx)
        specResult.tests.push(testResult)
      }
    }

    return aggregateOutcome(specResult, specResult.tests)
  } catch (e) {
    console.log(chalk.redBright(`ü§ï Processing ${spec.name}: ${e}`))
    throw e
  } finally {
    try {
      console.info(chalk.blueBright(`üëç Closing browser`))
      await closeBrowser()
    } catch (e) {
      console.warn(chalk.yellow(`ü§ï Closing browser: ${e}`))
    }
  }
}

async function runRemoteScript(testScript: TestScript, cfg: RuntimeConfig, testRunId: string) {
  const spec = testScript.scriptData.spec
  if (!spec) {
    throw "No spec found"
  }

  console.log(chalk.blueBright(`Running ${spec.name} (${spec.id})`))

  const browser = await createBrowser(cfg)
  const ctx: TestContext = {
    testRunId: testRunId,
    config: cfg,
    browser: browser,
    metadata: {},
    timeout: 10000,
  }

  try {
    const specResult: TestSpecRun = { ...outcomeStart(spec.name), specId: spec.id, tests: [] }

    for (const d of spec.definitions) {
      if (!cfg.test || d.name === cfg.test) {
        const testResult = await runTest(d, ctx)
        specResult.tests.push(testResult)
      }
    }

    return aggregateOutcome(specResult, specResult.tests)
  } catch (e) {
    console.log(chalk.redBright(`ü§ï Processing ${spec.name} (${spec.id}): ${e}`))
    throw e
  } finally {
    try {
      console.info(chalk.blueBright(`üëç Closing browser`))
      await closeBrowser()
    } catch (e) {
      console.warn(chalk.yellow(`ü§ï Closing browser: ${e}`))
    }
  }
}

export async function runTest(def: Definition, ctx: TestContext) {
  console.log(chalk.whiteBright(`  ${def.name}`))
  console.log(chalk.whiteBright(`  ${"=".repeat(def.name.length)}`))

  const testResult: TestDefinitionRun = { ...outcomeStart(def.name), definitionId: def.id, steps: [] }

  const calls: Map<string, Call[]> = new Map()
  for (const c of def.calls) {
    if (!calls.has(c.testSpecClauseId)) {
      calls.set(c.testSpecClauseId, [])
    }
    calls.get(c.testSpecClauseId)?.push(c)
  }

  for (const step of def.steps) {
    console.log(chalk.whiteBright(`  * ${step.clause} (L${step.line + 1})`))
    if (!calls.has(step.id)) {
      console.log(chalk.redBright(`    (‚úó) No function calls for ${step.clause}`))
      const res: TestStepRun = { ...outcomeStart(step.clause), stepId: step.id }
      testResult.steps.push(outcomeEnd(res, "error", "no matching calls found for step"))
      continue
    }

    const callList = calls.get(step.id)
    if (!callList || callList.length == 0) {
      const res: TestStepRun = { ...outcomeStart(step.clause), stepId: step.id }
      testResult.steps.push(outcomeEnd(res, "error", "no function calls found for step"))
      continue
    }

    ctx.currentStep = step
    for (const c of callList) {
      const fn = getTestRunnerCall(c.call)

      try {
        const params = JSON.parse(c.params)

        const result = await fn(ctx, params)
        testResult.steps.push(result)
        if (ctx.config.screenshots) {
          result.screenshot = `${ctx.config.screenshotsDir}/${ctx.testRunId}/${step.id}_${result.executionId}.png`
          const data = await ctx.browser.takeScreenshot()
          await fs.writeFile(result.screenshot, data, "base64")
        }

        if (result.result === "pass") {
          console.log(chalk.greenBright(`    (‚úì) Passed`))
        } else if (result.result === "warn") {
          console.log(chalk.yellowBright(`    (‚ö†) Warning: ${result.message}`))
        } else {
          console.log(chalk.redBright(`    (‚úó) Failed: ${result.message}`))
        }
      } catch (e) {
        console.log(chalk.redBright(`    (‚úó) Error: ${e}`))
        const browserLogs = await ctx.browser.manage().logs().get(logging.Type.BROWSER)
        for (const log of browserLogs) {
          console.log(chalk.yellowBright(`    (‚úó) ${log.message}`))
        }
      }
    }
  }

  return aggregateOutcome(testResult, testResult.steps)
}
