import axios from "axios"
import inquirer from "inquirer"
import { promises as fs } from "fs"

import { RuntimeConfig } from "../config"
import chalk from "chalk"

export async function InitTestabulous(cfg: RuntimeConfig) {
  const questions = [
    {
      type: "input",
      name: "projectId",
      message: "Project ID (copied from dashboard)",
      default: cfg.projectId,
    },
    {
      type: "input",
      name: "apiKey",
      message: "Project API key (copied from dashboard)",
      default: cfg.apiKey,
    },
  ]

  const answers = await inquirer.prompt(questions)
  if (!answers.projectId || !answers.apiKey) {
    throw new Error("Project ID and API key are required")
  }

  cfg.projectId = answers.projectId
  cfg.apiKey = answers.apiKey

  try {
    const { data } = await axios.post(`${cfg.apiBaseURL}/projects/${cfg.projectId}/ping`, undefined, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        Authorization: `Bearer ${cfg.apiKey}`,
      },
    })

    if (data !== "pong") {
      throw new Error("Project ID or API key is invalid")
    }

    const envFileContent = `TESTABULOUS_PROJECT_ID=${cfg.projectId}\nTESTABULOUS_API_KEY=${cfg.apiKey}\n`
    await fs.writeFile(".env.testabulous", envFileContent, { encoding: "utf-8" })

    const configFileContent = JSON.stringify({ envFile: ".env.testabulous" }, null, 2)
    await fs.writeFile("testabulous.config.json", configFileContent, { encoding: "utf-8" })

    const packageJson = await fs.readFile("package.json", { encoding: "utf-8" })
    const packageJsonObj = JSON.parse(packageJson)
    if (!packageJsonObj.scripts) {
      packageJsonObj.scripts = {}
    }
    if (!packageJsonObj.scripts["testabulous:cache"]) {
      packageJsonObj.scripts["test:cache"] = "testabulous cache --force"
    }
    if (!packageJsonObj.scripts["testabulous:test"]) {
      packageJsonObj.scripts["test"] = "testabulous test"
    }
    if (!packageJsonObj.scripts["testabulous:help"]) {
      packageJsonObj.scripts["test:help"] = "testabulous --help"
    }
    await fs.writeFile("package.json", JSON.stringify(packageJsonObj, null, 2), { encoding: "utf-8" })

    console.log(
      chalk.greenBright(
        "Your project ID and API key have been saved to .env.testabulous and this file referenced in testabulous.config.json. " +
          "You should make sure that .env.testabulous is added to your .gitignore file. " +
          "Alternatively, you can merge the TESTABULOUS_PROJECT_ID and TESTABULOUS_API_KEY " +
          "environment variables into another .env file and update the config file accordingly, or set them elsewhere.\n" +
          "You can now start creating your test files in ./testabulous/tests and call 'npm run test' to run your tests. " +
          "See https://docs.testabulous.com/category/getting-started for more information.",
      ),
    )
  } catch (e) {
    console.log(e)
    if (axios.isAxiosError(e)) {
      if (e.response && (e.response.status === 404 || e.response.status === 401)) {
        throw new Error("Project ID or API key is invalid")
      }
    }
    throw e
  }

  console.log(chalk.greenBright("üëç Project initialised"))
}
