import { eq, and } from "drizzle-orm"
import { drizzle } from "drizzle-orm/better-sqlite3"
import { migrate } from "drizzle-orm/better-sqlite3/migrator"
import Database from "better-sqlite3"
import path from "path"
import * as schema from "./schema"
import chalk from "chalk"

import {
  callsTable,
  clausesTable,
  definitionsTable,
  scriptDataTable,
  scriptsTable,
  testSpecsTable,
} from "../lib/schema"

import { Clause, ClauseType, Definition, TestScript, TestScriptData, TestSpec } from "./types"

export type DbConn = ReturnType<typeof drizzle>

let sqlite: ReturnType<typeof Database> | null = null
let drizzleInstance: DbConn | null = null

export const getDatabase = (dbPath: string) => {
  if (drizzleInstance === null) {
    if (sqlite === null) {
      sqlite = new Database(path.join(dbPath, "testabulous.db"))
    }
    drizzleInstance = drizzle(sqlite, { schema: schema, logger: false })
  }
  return drizzleInstance
}

export const closeDatabase = () => {
  console.log(chalk.yellow(`üëç Closing database`))
  if (drizzleInstance != null) {
    drizzleInstance = null
  }
  if (sqlite != null) {
    sqlite.close()
    sqlite = null
  }
}

export const migrateDatabase = async (dbPath: string) => {
  const db = getDatabase(dbPath)
  try {
    const dir = path.join(__dirname, "..", "drizzle")
    console.log(chalk.greenBright(`Migrating databases from: ${dir}`))
    await migrate(db, { migrationsFolder: dir })
  } catch (e) {
    console.log(chalk.redBright(`ü§ï Migrating database: ${e}`))
  }
}

export async function findScriptByHash(hash: string, db: DbConn) {
  const dataRows = await db.select().from(scriptDataTable).where(eq(scriptDataTable.hash, hash))
  if (dataRows.length === 0) {
    return null
  }
  const scriptData = dataRows[0] as TestScriptData
  const scriptRows = await db.select().from(scriptsTable).where(eq(scriptsTable.id, scriptData.scriptId))
  if (scriptRows.length === 0) {
    return null
  }
  const script = scriptRows[0] as TestScript
  script.scriptData = scriptData
  return script
}

export async function findScriptDataByHash(hash: string, db: DbConn) {
  const rows = await db.select().from(scriptDataTable).where(eq(scriptDataTable.hash, hash))
  if (rows.length === 0) {
    return null
  }
  return rows[0] as TestScriptData
}

export async function findTestSpecByScriptDataId(scriptDataId: string, db: DbConn) {
  const rows = await db.select().from(testSpecsTable).where(eq(testSpecsTable.scriptDataId, scriptDataId))
  if (rows.length === 0) {
    return null
  }
  const testSpec = rows[0] as TestSpec

  const topGivens = await db
    .select()
    .from(clausesTable)
    .where(and(eq(clausesTable.parentId, testSpec.id), eq(clausesTable.type, ClauseType.TopLevelGiven)))
    .orderBy(clausesTable.line)
  testSpec.givens = topGivens as Clause[]

  const defs = await db
    .select()
    .from(definitionsTable)
    .where(eq(definitionsTable.testSpecId, testSpec.id))
    .orderBy(definitionsTable.line)
  testSpec.definitions = defs as Definition[]

  for (const d of testSpec.definitions) {
    const givens = await db
      .select()
      .from(clausesTable)
      .where(and(eq(clausesTable.parentId, d.id), eq(clausesTable.type, ClauseType.TestGiven)))
      .orderBy(clausesTable.line)
    d.givens = givens as Clause[]

    const steps = await db
      .select()
      .from(clausesTable)
      .where(and(eq(clausesTable.parentId, d.id), eq(clausesTable.type, ClauseType.Step)))
      .orderBy(clausesTable.line)
    d.steps = steps as Clause[]

    const calls = await db.select().from(callsTable).where(eq(callsTable.testSpecDefinitionId, d.id))
    d.calls = calls
  }

  return testSpec
}
