import fs from "fs/promises"
import path from "path"
import chalk from "chalk"

import { RuntimeConfig, fileExists } from "../config"

export async function getTargetFiles(cfg: RuntimeConfig) {
  let testFiles: string[] = []
  if (cfg.file) {
    const p = path.isAbsolute(cfg.file) ? cfg.file : path.resolve(cfg.testsDir, cfg.file)
    const exists = await fileExists(p)
    if (exists) {
      testFiles.push(p)
      return testFiles
    }
    throw `File ${cfg.file} not found`
  } else {
    console.log(chalk.blueBright(`‚è≥ Looking for test files in ${cfg.testsDir}`))
    testFiles = await walkDirs(cfg.testsDir)
    console.log(chalk.blueBright(`üëç Found ${testFiles.length} test files`))
  }

  if (testFiles.length === 0) {
    throw "No test files found"
  }

  return testFiles
}

async function walkDirs(source: string) {
  const dirs = await fs.readdir(source)

  const files: string[] = []
  for (const d of dirs) {
    const p = path.join(source, d)
    const stat = await fs.stat(p)
    if (stat.isDirectory()) {
      const children = await walkDirs(p)
      files.push(...children)
    } else if (p.toLocaleLowerCase().endsWith(".md")) {
      files.push(p)
    }
  }

  return files
}
