import fs from "fs/promises"
import chalk from "chalk"
import { eq, inArray } from "drizzle-orm"
import { createHash } from "node:crypto"
import axios from "axios"
import { RuntimeConfig } from "../config"
import { closeDatabase, DbConn, getDatabase, findScriptByHash } from "../lib/db"
import { TestScript, Status } from "../lib/types"
import {
  callsTable,
  clausesTable,
  definitionsTable,
  scriptDataTable,
  scriptsTable,
  testSpecsTable,
} from "../lib/schema"
import { getTargetFiles } from "../lib/utils"

export async function Cache(cfg: RuntimeConfig) {
  try {
    const testFiles = await getTargetFiles(cfg)
    const db = getDatabase(cfg.cacheDir)
    await cacheFiles(testFiles, cfg, db)
  } catch (e) {
    console.log(chalk.redBright(`ü§ï Caching files failed: ${e}`))
  } finally {
    closeDatabase()
  }
}

export async function cacheFiles(files: string[], cfg: RuntimeConfig, db: DbConn) {
  const scripts: TestScript[] = []
  for (const f of files) {
    scripts.push(await cacheFile(f, cfg, db))
  }

  for (const script of scripts) {
    if (script.scriptData.status === Status.Active) {
      continue
    }
    console.log(chalk.blueBright(`Waiting for processing of ${script.name} to complete`))
    await pollForActiveScript(script.id, script.scriptData.id, cfg, db)
  }
}

async function cacheFile(file: string, cfg: RuntimeConfig, db: DbConn) {
  console.log(chalk.blueBright(`Processing ${file}`))
  if (cfg.force) {
    console.log(chalk.blueBright(`Forcing re-cache of ${file}`))
  }

  try {
    const scriptText = await fs.readFile(file, { encoding: "utf-8" })
    const req = {
      uri: `file://${file}`,
      data: scriptText,
      scriptType: "markdown",
      forceParse: cfg.force,
    }

    const hash = createHash("sha256").update(scriptText).digest("base64")
    const existingScript = await findScriptByHash(hash, db)
    if (!cfg.force) {
      if (existingScript && existingScript.status === Status.Active) {
        console.log(chalk.greenBright(`üëç ${file} already cached`))
        return existingScript
      }
    }

    const { data: script } = await axios.post<TestScript>(`${cfg.apiBaseURL}/projects/${cfg.projectId}/scripts`, req, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        Authorization: `Bearer ${cfg.apiKey}`,
      },
    })
    await saveScript(script, db)

    console.log(chalk.greenBright(`üëç ${file} cached`))
    return script
  } catch (e) {
    if (axios.isAxiosError(e)) {
      if (e.response && e.response.status === 402) {
        throw "You have insufficient credits, please top up your account"
      }
    }
    console.log(chalk.redBright(`ü§ï Processing ${file}: ${e}`))
    throw e
  }
}

async function pollForActiveScript(scriptId: string, scriptDataId: string, cfg: RuntimeConfig, db: DbConn) {
  return new Promise<TestScript>((resolve, reject) => {
    const start = new Date()
    const timer = setInterval(async () => {
      try {
        console.log(chalk.blueBright(`Polling for script ${scriptId}`))
        const { status, data: script } = await axios.get<TestScript>(
          `${cfg.apiBaseURL}/projects/${cfg.projectId}/scripts/${scriptId}/data/${scriptDataId}`,
          {
            headers: {
              "Content-Type": "application/json",
              Accept: "application/json",
              Authorization: `Bearer ${cfg.apiKey}`,
            },
          },
        )
        if (status !== 200) {
          throw `Server returned ${status}`
        }
        await saveScript(script, db)

        if (script.scriptData.status === Status.Active) {
          console.log(chalk.greenBright(`üëç ${script.id} cached`))
          clearTimeout(timer)
          resolve(script)
          return
        }

        const now = new Date()
        if (now.getTime() - start.getTime() > 60000) {
          clearTimeout(timer)
          reject(`Timeout waiting for completion of ${scriptId}`)
        }
      } catch (e) {
        console.log(chalk.redBright(`ü§ï Polling for active script ${scriptId}: ${e}`))
        throw e
      }
    }, 5000)
  })
}

async function saveScript(script: TestScript, db: DbConn) {
  await db.transaction(async (tx) => {
    const existingScript = await tx.select().from(scriptsTable).where(eq(scriptsTable.id, script.id))
    if (existingScript.length === 0) {
      await tx.insert(scriptsTable).values({
        id: script.id,
        createdAt: script.createdAt,
        updatedAt: script.updatedAt,
        status: script.status,
        name: script.name,
        uri: script.uri,
        scriptType: script.scriptType,
      })
    } else {
      await tx
        .update(scriptsTable)
        .set({
          updatedAt: script.updatedAt,
          status: script.status,
          name: script.name,
        })
        .where(eq(scriptsTable.id, script.id))
    }

    const existingData = await tx.select().from(scriptDataTable).where(eq(scriptDataTable.id, script.scriptData.id))
    if (existingData.length === 0) {
      await tx.insert(scriptDataTable).values({
        id: script.scriptData.id,
        createdAt: script.scriptData.createdAt,
        updatedAt: script.scriptData.updatedAt,
        status: script.scriptData.status,
        hash: script.scriptData.hash,
        scriptId: script.id,
      })
    } else {
      await tx
        .update(scriptDataTable)
        .set({
          updatedAt: script.scriptData.updatedAt,
          status: script.scriptData.status,
        })
        .where(eq(scriptDataTable.id, script.scriptData.id))
    }

    if (script.scriptData.spec) {
      const existingSpec = await tx
        .select()
        .from(testSpecsTable)
        .where(eq(testSpecsTable.id, script.scriptData.spec.id))
      if (existingSpec.length === 0) {
        await tx.insert(testSpecsTable).values({
          id: script.scriptData.spec.id,
          createdAt: script.scriptData.spec.createdAt,
          updatedAt: script.scriptData.spec.updatedAt,
          status: script.scriptData.spec.status,
          scriptDataId: script.scriptData.id,
          name: script.scriptData.spec.name,
          description: script.scriptData.spec.description,
        })
      } else {
        await tx
          .update(testSpecsTable)
          .set({
            updatedAt: script.scriptData.spec.updatedAt,
            status: script.scriptData.spec.status,
            name: script.scriptData.spec.name,
            description: script.scriptData.spec.description,
          })
          .where(eq(testSpecsTable.id, script.scriptData.spec.id))
      }

      const q1 = tx
        .select({ data: definitionsTable.id })
        .from(definitionsTable)
        .where(eq(definitionsTable.testSpecId, script.scriptData.spec.id))
      await tx.delete(clausesTable).where(inArray(clausesTable.parentId, q1))
      await tx.delete(callsTable).where(inArray(callsTable.testSpecDefinitionId, q1))

      await tx.delete(definitionsTable).where(eq(definitionsTable.testSpecId, script.scriptData.spec.id))
      await tx.delete(clausesTable).where(eq(clausesTable.parentId, script.scriptData.spec.id))

      if (script.scriptData.spec.givens) {
        for (const g of script.scriptData.spec.givens) {
          await tx.insert(clausesTable).values({
            id: g.id,
            createdAt: g.createdAt,
            updatedAt: g.updatedAt,
            status: g.status,
            parentId: script.scriptData.spec.id,
            type: g.type,
            line: g.line,
            clause: g.clause,
          })
        }
      }

      for (const d of script.scriptData.spec.definitions) {
        await tx.insert(definitionsTable).values({
          id: d.id,
          createdAt: d.createdAt,
          updatedAt: d.updatedAt,
          status: d.status,
          testSpecId: d.testSpecId,
          name: d.name,
          line: d.line,
          type: d.type,
        })

        if (d.givens) {
          for (const g of d.givens) {
            await tx.insert(clausesTable).values({
              id: g.id,
              createdAt: g.createdAt,
              updatedAt: g.updatedAt,
              status: g.status,
              parentId: d.id,
              type: g.type,
              line: g.line,
              clause: g.clause,
            })
          }
        }

        if (d.steps) {
          for (const s of d.steps) {
            await tx.insert(clausesTable).values({
              id: s.id,
              createdAt: s.createdAt,
              updatedAt: s.updatedAt,
              status: s.status,
              parentId: d.id,
              type: s.type,
              line: s.line,
              clause: s.clause,
            })
          }
        }

        if (d.calls) {
          for (const c of d.calls) {
            await tx.insert(callsTable).values({
              id: c.id,
              createdAt: c.createdAt,
              updatedAt: c.updatedAt,
              status: c.status,
              testSpecDefinitionId: c.testSpecDefinitionId,
              testSpecClauseId: c.testSpecClauseId,
              externalId: c.externalId,
              call: c.call,
              params: c.params,
            })
          }
        }
      }
    }
  })
}
