Handlebars.registerHelper("isPassed", function (value: TestOutcome) {
  return value.result === "pass"
})

Handlebars.registerHelper("isFailed", function (value: TestOutcome) {
  return value.result === "fail" || value.result === "error" || value.result === "warn"
})

Handlebars.registerHelper("isOthered", function (value: TestOutcome) {
  return !(value.result === "pass" || value.result === "fail" || value.result === "error" || value.result === "warn")
})

Handlebars.registerHelper("renderName", function (test: TestOutcome) {
  const icon = icons[test.result]
  let colour = "text-blue-700"
  switch (test.result) {
    case "pass":
      colour = "text-green-700"
      break
    case "fail":
      colour = "text-red-700"
      break
    case "error":
      colour = "text-red-700"
      break
    case "warn":
      colour = "text-yellow-700"
      break
  }
  return `<div class="flex flex-row gap-2 ${colour} items-center"><div>${icon}</div><div class="">${test.name}</div></div>`
})

const templateMarkup = fs.readFileSync(path.resolve(__dirname, "../resources/testrun.hbs"), "utf8")
const template = Handlebars.compile(templateMarkup)

export function renderTestResult(testRun: TestRun) {
  return template({
    res: testRun,
    testCount: testRun.passCount + testRun.failCount + testRun.errorCount + testRun.warnCount + testRun.skipCount,
  })
}
