#!/usr/bin/env node
/* eslint-disable @typescript-eslint/no-explicit-any */

import chalk from "chalk"
import figlet from "figlet"
import { Command } from "commander"
import { TestRunner } from "./runner"
import { WebUI } from "./ui"
import { Cache } from "./cache"
import { RuntimeConfig, BuildConfig as buildConfig } from "./config"
import { migrateDatabase } from "./lib/db"
import { InitTestabulous } from "./init"

console.log(chalk.greenBright(figlet.textSync("Testabulous")))

const program = new Command()
program.exitOverride((err) => {
  console.log(chalk.redBright(`ü§ï ${err}`))
  process.exit(-1)
})

program
  .version("1.0.0")
  .description("Testabulous E2E Testing Framework")
  .option("-c, --config-file <value>", "Local config file [./testabulous.config.json]")
  .option("-e, --env-file <value>", "File to load environment variables from")
  .option("-p, --project-id <value>", "Project ID")
  .option("-k, --api-key <value>", "API key")
  .option("-r, --root-dir <value>", "Test root directory [./testabulous]")

program
  .command("init")
  .description("Initialise testabulous project")
  .action(async () => {
    try {
      const cfg = await buildConfig(program.opts(), {})
      await migrateDatabase(cfg.cacheDir)

      await InitTestabulous(cfg)
    } catch (e) {
      console.log(chalk.redBright(`ü§ï Initialisation failed: ${e}`))
      process.exit(-1)
    }
  })

program
  .command("cache")
  .option("-f, --file <value>", "Specific test file to cache")
  .option("-F, --force", "Force re-cache of all test files")
  .description("Parse and cache test files")
  .action(async (localOpts: any) => {
    try {
      let cfg: RuntimeConfig
      try {
        cfg = await buildConfig(program.opts(), localOpts)
        await migrateDatabase(cfg.cacheDir)
      } catch (e) {
        console.log(chalk.redBright(`ü§ï Initialisation failed: ${e}`))
        process.exit(-1)
      }

      await Cache(cfg)
    } catch (e) {
      console.log(chalk.redBright(`ü§ï ${e}`))
      process.exit(-1)
    }
  })

program
  .command("test")
  .option("-f, --file <value>", "Specific test file to execute")
  .option("-t, --test <value>", "Specific test to execute")
  .description("Start testabulous test runner")
  .action(async (localOpts: any) => {
    let cfg: RuntimeConfig
    try {
      cfg = await buildConfig(program.opts(), localOpts)
      await migrateDatabase(cfg.cacheDir)
    } catch (e) {
      console.log(chalk.redBright(`ü§ï Initialisation failed: ${e}`))
      process.exit(-1)
    }

    TestRunner("local", cfg)
      .then((results) => {
        switch (results.result) {
          case "pass":
          case "skip":
            process.exit(0)
            break
          case "warn":
            process.exit(1)
            break
          case "fail":
            process.exit(2)
            break
          case "error":
            process.exit(3)
            break
        }
      })
      .catch(() => {
        process.exit(3)
      })
  })

program
  .command("remote")
  .description("Start testabulous test runner for remote run")
  .action(async (localOpts: any) => {
    let cfg: RuntimeConfig
    try {
      cfg = await buildConfig(program.opts(), localOpts)
      await migrateDatabase(cfg.cacheDir)
    } catch (e) {
      console.log(chalk.redBright(`ü§ï Initialisation failed: ${e}`))
      process.exit(-1)
    }

    TestRunner("remote", cfg)
      .then((results) => {
        switch (results.result) {
          case "pass":
          case "skip":
            process.exit(0)
            break
          case "warn":
            process.exit(1)
            break
          case "fail":
            process.exit(2)
            break
          case "error":
            process.exit(3)
            break
        }
      })
      .catch(() => {
        process.exit(3)
      })
  })

program
  .command("open")
  .description("Open testabulous UI")
  .option("-P, --port <value>", "Port to run Testabulous UI [4001]")
  .action(async (localOpts: any) => {
    let cfg: RuntimeConfig
    try {
      cfg = await buildConfig(program.opts(), localOpts)
      await migrateDatabase(cfg.cacheDir)
    } catch (e) {
      console.log(chalk.redBright(`ü§ï Initialisation failed: ${e}`))
      process.exit(-1)
    }

    WebUI(cfg)
      .then(() => {
        console.log(chalk.greenBright(`Lingobt UI terminated`))
      })
      .catch((e) => {
        console.log(chalk.redBright(`ü§ï ${e}`))
        process.exit(-1)
      })
  })

try {
  program.parse(process.argv)
} catch (e) {
  console.log(chalk.redBright(`ü§ï ${e}`))
  process.exit(-1)
}
