import { setInterval } from "timers/promises"
import { By, WebElement } from "selenium-webdriver"
import { error } from "selenium-webdriver"
import { createId } from "@paralleldrive/cuid2"
import { format } from "date-fns"

import { TestContext, TestOutcome, TestStepRun, TimerCallback, TestResult, TestRun } from "./types"
import { RuntimeConfig } from "../config"
import axios from "axios"
import chalk from "chalk"

const defaultAlphabet = "abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789"

const includeAlphabet = new Map([
  ["l", "abcdefghijklmnopqrstuvwxyz"],
  ["L", "ABCDEFGHIJKLMNOPQRSTUVWXYZ"],
  ["n", "0123456789"],
  ["s", "!@#$%^&*()_+"],
])

function randomString(spec: string) {
  let lenSpec = ""
  let charSpec = ""
  for (const s of spec) {
    if (/\d/.test(s)) {
      lenSpec += s
      continue
    }
    if (includeAlphabet.has(s)) {
      charSpec += includeAlphabet.get(s)
    }
  }

  let len = 8
  if (lenSpec > "") {
    len = parseInt(lenSpec)
  }

  let chars = charSpec
  if (charSpec === "") {
    chars = defaultAlphabet
  }

  let result = ""
  for (let i = 0; i < len; i++) {
    result += chars.charAt(Math.floor(Math.random() * chars.length))
  }

  return result
}

const functions = new Map()
functions.set("_random", randomString)

export function dereferenceArgument(ctx: TestContext, val: string) {
  let result = val
  const matches = val.match(/\{\{.+?\}\}/g)
  if (matches) {
    for (const m of matches) {
      const key = m
        .substring(2, m.length - 2)
        .substring(0, m.length - 2)
        .trim()
      if (ctx.metadata[key]) {
        result = result.replace(m, ctx.metadata[key])
        continue
      }
      if (/^['"‚Äú‚Äù].*['"‚Äú‚Äù]$/.test(key)) {
        result = key.substring(1, key.length - 1)
        continue
      }
      if (/_\w+\(.*?\)/.test(key)) {
        const els = key.split(/[()]/).filter((el) => el.length > 0)
        if (functions.has(els[0])) {
          const fn = functions.get(els[0])
          if (els.length > 1) {
            result = result.replace(m, fn(els[1]))
            continue
          }
          result = result.replace(m, fn())
        }
      }
    }
  }

  return result
}

export async function executeWithTimeout(cb: TimerCallback, timeout: number) {
  return new Promise<TestStepRun>((resolve, reject) => {
    ;(async () => {
      let lastResult: TestStepRun | null = null
      for await (const start of setInterval(250, new Date().valueOf())) {
        const elapsed = new Date().valueOf() - start
        if (elapsed > timeout) {
          if (lastResult) {
            resolve(lastResult)
            return
          }
          reject(new Error("time out executing step"))
          return
        }
        const res = await cb()
        if (res) {
          if (res.result === "pass") {
            resolve(res)
            return
          }
          lastResult = res
        }
      }
    })()
  })
}

export async function findElement(ctx: TestContext, identifierType: string, identifier: string, timeout: number) {
  return new Promise<WebElement>((resolve, reject) => {
    ;(async () => {
      for await (const start of setInterval(100, new Date().valueOf())) {
        const elapsed = new Date().valueOf() - start
        if (elapsed > timeout) {
          reject(new Error(`time out search for element: ${identifierType}=${identifier}`))
          break
        }
        let el: WebElement | null = null
        let t = identifierType.toLowerCase()
        if (/^aria[- _]/.test(t)) {
          t = "aria"
        }

        try {
          switch (t) {
            case "id":
              el = await ctx.browser.findElement(By.id(identifier))
              break
            case "class":
              el = await ctx.browser.findElement(By.className(identifier))
              break
            case "css selector":
            case "css":
              el = await ctx.browser.findElement(By.css(identifier))
              break
            case "field":
              el = await ctx.browser.findElement(By.name(identifier))
              break
            case "label":
              el = await findByLabel(ctx, identifierType, identifier)
              break
            case "name":
              el = await ctx.browser.findElement(By.css(`*[name='${identifier}']`))
              break
            case "text":
              el = await ctx.browser.findElement(By.xpath(`//*[text()='${identifier}']`))
              break
            case "button":
              el = await ctx.browser.findElement(By.xpath(`//button[text()='${identifier}']`))
              break
            case "aria":
              el = await ctx.browser.findElement(
                By.css(`*[${identifierType.toLowerCase().replaceAll(/[ _]/g, "-")}='${identifier}']`),
              )
              break
            default:
              throw new Error(`unknown identifier type: ${identifierType}`)
          }
        } catch (e) {
          if (e instanceof error.NoSuchElementError || e instanceof error.StaleElementReferenceError) {
            continue
          }
          reject(e)
          return
        }
        if (!el) {
          continue
        }
        resolve(el)
      }
    })()
  })
}

export async function findByLabel(ctx: TestContext, identifierType: string, identifier: string) {
  const el = await ctx.browser.findElement(By.xpath(`//*[contains(text(),'${identifier}')]`))
  if (!el) {
    return null
  }

  const forAttr = await el.getAttribute("for")
  if (forAttr) {
    return await ctx.browser.findElement(By.id(forAttr))
  }

  return el
}

export async function checkForClass(el: WebElement, clazz: string) {
  const classes = await el.getAttribute("class")
  if (!classes) {
    return false
  }
  const list = classes.toString().split(" ")
  return list.indexOf(clazz) >= 0
}

export function formatNumber(val: number) {
  let result = val.toString()
  while (result.length < 5) {
    result = " " + result
  }
  return result
}

export function outcomeStart(name?: string) {
  const startDate = new Date()
  return {
    executionId: createId(),
    name: name || `Test run: ${format(startDate, "yyyy-MM-dd HH:mm:ss")}`,
    result: "fail" as TestResult,
    startDate,
    duration: 0,
    passCount: 0,
    failCount: 0,
    skipCount: 0,
    warnCount: 0,
    errorCount: 0,
  } as TestOutcome
}

export function outcomeEnd<T extends TestOutcome>(outcome: T, result: TestResult, message?: string): T {
  outcome.result = result
  switch (result) {
    case "pass":
      outcome.passCount++
      break
    case "fail":
      outcome.failCount++
      break
    case "skip":
      outcome.skipCount++
      break
    case "warn":
      outcome.warnCount++
      break
    case "error":
      outcome.errorCount++
      break
  }
  outcome.message = message
  outcome.endDate = new Date()
  outcome.duration = outcome.endDate.getTime() - outcome.startDate.getTime()

  return outcome
}

export function aggregateOutcome<T extends TestOutcome, U extends TestOutcome>(outcome: T, outcomes: U[]): T {
  outcome.endDate = new Date()
  outcome.duration = outcome.endDate.getTime() - outcome.startDate.getTime()

  for (const o of outcomes) {
    outcome.passCount += o.passCount
    outcome.failCount += o.failCount
    outcome.skipCount += o.skipCount
    outcome.warnCount += o.warnCount
    outcome.errorCount += o.errorCount
  }

  if (outcome.errorCount > 0) {
    outcome.result = "error"
  } else if (outcome.failCount > 0) {
    outcome.result = "fail"
  } else if (outcome.passCount == 0 && outcome.warnCount > 0) {
    outcome.result = "warn"
  } else if (outcome.passCount == 0 && outcome.skipCount > 0) {
    outcome.result = "skip"
  } else {
    outcome.result = "pass"
  }
  return outcome
}

export async function uploadTestResult(cfg: RuntimeConfig, testRun: TestRun) {
  if (!cfg.uploadResults) {
    return
  }

  const { status } = await axios.post<string>(`${cfg.apiBaseURL}/projects/${cfg.projectId}/results`, testRun, {
    headers: {
      "Content-Type": "application/json",
      Accept: "application/json",
      Authorization: `Bearer ${cfg.apiKey}`,
    },
  })

  if (status !== 200) {
    throw `Server returned ${status}`
  }

  console.log(chalk.yellow(`üëç Uploaded results to Testabulous`))
}
