/* eslint-disable @typescript-eslint/no-explicit-any */
import { setInterval } from "timers/promises"
import path from "path"
import chalk from "chalk"
import { ExternalScript, TestContext, TestContextArguments, TestRunnerCall, TestStepRun } from "./types"
import { checkForClass, dereferenceArgument, executeWithTimeout, findElement, outcomeEnd, outcomeStart } from "./utils"
import { fileExists } from "../config"
import { runTest } from "."

const testRunnerCalls: Map<string, TestRunnerCall> = new Map()

export function registerTestRunnerCall(name: string, fn: TestRunnerCall) {
  if (testRunnerCalls.has(name)) {
    throw new Error(`test runner call already registered: ${name}`)
  }
  testRunnerCalls.set(name, fn)
}

export function getTestRunnerCall(name: string) {
  if (!testRunnerCalls.has(name)) {
    throw new Error(`test runner call not found: ${name}`)
  }
  return testRunnerCalls.get(name)!
}

const validPageProperties = new Set<string>()
validPageProperties.add("title")

testRunnerCalls.set("navigate_to_page", async (ctx: TestContext, args: TestContextArguments) => {
  const res: TestStepRun = { ...outcomeStart(ctx.currentStep!.clause), stepId: ctx.currentStep!.id }

  if (!args.url) {
    throw new Error(`missing argument: url`)
  }

  try {
    const currentUrl = ctx.browser.getCurrentUrl()
    if (currentUrl === args.url) {
      await ctx.browser.navigate().refresh()
    } else {
      await ctx.browser.get(args.url)
    }

    for await (const start of setInterval(1000, new Date().valueOf())) {
      const elapsed = new Date().valueOf() - start
      if (elapsed > ctx.timeout) {
        throw new Error("time out waiting for navigation")
      }
      const state = await ctx.browser.executeScript(`return document.readyState`)
      if (state === "complete" || state === "interactive") {
        break
      }
    }

    await ctx.browser.executeScript(`window.TESTABULOUS_testRunId = "${ctx.testRunId}"`)

    outcomeEnd(res, "pass")
  } catch (e) {
    console.log(chalk.redBright(`ü§ï ${e}`))
    outcomeEnd(res, "error", e!.toString())
  }

  return res
})

testRunnerCalls.set("check_for_page_to_have_property", async (ctx: TestContext, args: TestContextArguments) => {
  if (!args.property) {
    throw new Error(`missing argument: property`)
  }
  if (!args.value) {
    throw new Error(`missing argument: value`)
  }
  const property = args.property.toString().toLowerCase()
  if (!validPageProperties.has(property)) {
    throw new Error(`unsupported identifier: ${property}`)
  }

  const res = await executeWithTimeout(async () => {
    const res: TestStepRun = { ...outcomeStart(ctx.currentStep!.clause), stepId: ctx.currentStep!.id }
    try {
      const state = await ctx.browser.executeScript(`return document.readyState`)
      if (state !== "complete") {
        return undefined
      }

      const value = await ctx.browser.executeScript(`return document.${property}`)
      if (value == args.value) {
        outcomeEnd(res, "pass")
      } else {
        outcomeEnd(res, "fail", `found='${value}', expected='${args.value}'`)
      }

      res.endDate = new Date()
      res.duration = res.endDate.valueOf() - res.startDate.valueOf()

      return res
    } catch (e) {
      console.log(chalk.redBright(`ü§ï ${e}`))
      res.result = "error"
      res.message = e!.toString()
    }
  }, ctx.timeout)

  return res
})

testRunnerCalls.set("click_on_element", async (ctx: TestContext, args: TestContextArguments) => {
  if (!args.identifier) {
    throw new Error(`missing argument: identifier`)
  }
  if (!args.identifierType) {
    throw new Error(`missing argument: identifierType`)
  }

  const res = await executeWithTimeout(async () => {
    const res: TestStepRun = { ...outcomeStart(ctx.currentStep!.clause), stepId: ctx.currentStep!.id }

    try {
      const el = await findElement(ctx, args.identifierType, args.identifier, ctx.timeout)
      if (!el) {
        outcomeEnd(res, "error", "element not found")
        return res
      }

      await el.click()
      outcomeEnd(res, "pass")

      return res
    } catch (e) {
      console.log(chalk.redBright(`ü§ï ${e}`))
      outcomeEnd(res, "error", e!.toString())
    }
  }, ctx.timeout)

  return res
})

