async function checkForState(ctx: TestContext, args: TestContextArguments, expected: boolean) {
  if (!args.identifier) {
    throw new Error(`missing argument: identifier`)
  }
  if (!args.identifierType) {
    throw new Error(`missing argument: identifierType`)
  }
  if (!args.state) {
    throw new Error(`missing argument: state`)
  }
  if (!args.stateType) {
    throw new Error(`missing argument: stateType`)
  }

  const res = await executeWithTimeout(async () => {
    const res: TestStepRun = { ...outcomeStart(ctx.currentStep!.clause), stepId: ctx.currentStep!.id }

    try {
      const el = await findElement(ctx, args.identifierType, args.identifier, ctx.timeout)
      if (!el) {
        return
      }

      let hasState = false
      switch (args.stateType) {
        case "class":
          hasState = await checkForClass(el, args.state)
          break
        default:
          throw new Error("unknown state type")
      }

      if (hasState === expected) {
        outcomeEnd(res, "pass")
      } else {
        outcomeEnd(res, "fail", `unexpected state: found='${hasState}', expected='${expected}'`)
      }

      return res
    } catch (e) {
      console.log(chalk.redBright(`ü§ï ${e}`))
      outcomeEnd(res, "error", e!.toString())
    }
  }, ctx.timeout)

  return res
}

testRunnerCalls.set("element_has_property_or_state", async (ctx: TestContext, args: TestContextArguments) => {
  return checkForState(ctx, args, true)
})

testRunnerCalls.set("element_does_not_have_property_or_state", async (ctx: TestContext, args: TestContextArguments) => {
  return checkForState(ctx, args, false)
})

testRunnerCalls.set("element_has_text", async (ctx: TestContext, args: TestContextArguments) => {
  if (!args.identifier) {
    throw new Error(`missing argument: identifier`)
  }
  if (!args.identifierType) {
    throw new Error(`missing argument: identifierType`)
  }
  if (!args.text) {
    throw new Error(`missing argument: text`)
  }

  const res = await executeWithTimeout(async () => {
    const res: TestStepRun = { ...outcomeStart(ctx.currentStep!.clause), stepId: ctx.currentStep!.id }

    try {
      const el = await findElement(ctx, args.identifierType, args.identifier, ctx.timeout)
      if (!el) {
        return
      }

      const text = await el.getText()
      if (text === args.text) {
        outcomeEnd(res, "pass")
      } else {
        outcomeEnd(res, "fail", `invalid text: found='${text}', expected='${args.text}'`)
      }

      return res
    } catch (e) {
      console.log(chalk.redBright(`ü§ï ${e}`))
      outcomeEnd(res, "error", e!.toString())
    }
  }, ctx.timeout)

  return res
})

testRunnerCalls.set("wait_for_time_period", async (ctx: TestContext, args: TestContextArguments) => {
  const res: TestStepRun = { ...outcomeStart(ctx.currentStep!.clause), stepId: ctx.currentStep!.id }

  try {
    let time = parseInt(args.time)
    switch (args.unit) {
      case "s":
      case "second":
      case "seconds":
        time = time * 1000
        break
      case "m":
      case "minute":
      case "minutes":
        time = time * 1000 * 60
        break
      case "h":
      case "hour":
      case "hours":
        time = time * 1000 * 60 * 60
        break
      default:
        throw new Error(`unsupported time unit: ${args.unit}`)
    }

    await new Promise((resolve) => setTimeout(resolve, time))
    outcomeEnd(res, "pass")
  } catch (e) {
    console.log(chalk.redBright(`ü§ï ${e}`))
    outcomeEnd(res, "error", e!.toString())
  }

  return res
})

