testRunnerCalls.set("check_url_path", async (ctx: TestContext, args: TestContextArguments) => {
  const res: TestStepRun = { ...outcomeStart(ctx.currentStep!.clause), stepId: ctx.currentStep!.id }

  try {
    const urlRaw = await ctx.browser.getCurrentUrl()
    const url = new URL(urlRaw)
    if (url.pathname === args.path) {
      outcomeEnd(res, "pass")
    } else {
      outcomeEnd(res, "fail", `invalid url path: found='${url.pathname}', expected='${args.path}'`)
    }
  } catch (e) {
    console.log(chalk.redBright(`ü§ï ${e}`))
    outcomeEnd(res, "error", e!.toString())
  }

  return res
})

testRunnerCalls.set("wait_for_element_to_have_value", async (ctx: TestContext) => {
  const res: TestStepRun = { ...outcomeStart(ctx.currentStep!.clause), stepId: ctx.currentStep!.id }
  outcomeEnd(res, "error", "not implemented")
  return res
})

testRunnerCalls.set("load_value_from_environment_variable", async (ctx: TestContext, args: TestContextArguments) => {
  const res: TestStepRun = { ...outcomeStart(ctx.currentStep!.clause), stepId: ctx.currentStep!.id }
  if (!args.variableName) {
    throw new Error(`missing argument: variableName`)
  }
  if (!args.envVarName) {
    throw new Error(`missing argument: envVariableName`)
  }

  try {
    const envVarName = args.envVarName.startsWith("$") ? args.envVarName.slice(1) : args.envVarName
    const value = process.env[envVarName] || ""

    ctx.metadata[args.variableName] = dereferenceArgument(ctx, value)
    outcomeEnd(res, "pass")
  } catch (e) {
    console.log(chalk.redBright(`ü§ï ${e}`))
    outcomeEnd(res, "error", e!.toString())
  }
  return res
})

testRunnerCalls.set("execute_script", async (ctx: TestContext, args: TestContextArguments) => {
  const res: TestStepRun = { ...outcomeStart(ctx.currentStep!.clause), stepId: ctx.currentStep!.id }
  if (!args.scriptName) {
    throw new Error(`missing argument: variableName`)
  }

  const def = ctx.config.procedures.find((p) => p.name === args.scriptName)
  if (def) {
    const testResult = await runTest(def, ctx)
    outcomeEnd(res, testResult.result, testResult.message)
    return res
  }

  const scriptPath = path.join(ctx.config.scriptsDir, args.scriptName)
  if (!fileExists(scriptPath)) {
    throw new Error(`missing script: ${scriptPath}`)
  }

  try {
    const { default: scriptModule } = (await import(scriptPath)) as { default: ExternalScript }
    const state = await scriptModule(ctx)
    outcomeEnd(res, state.result, state.message)
  } catch (e) {
    console.log(chalk.redBright(`ü§ï ${e}`))
    outcomeEnd(res, "error", e!.toString())
  }
  return res
})

testRunnerCalls.set("wait_for_navigation", async (ctx: TestContext) => {
  const res: TestStepRun = { ...outcomeStart(ctx.currentStep!.clause), stepId: ctx.currentStep!.id }

  try {
    for await (const start of setInterval(1000, new Date().valueOf())) {
      const elapsed = new Date().valueOf() - start
      if (elapsed > ctx.timeout) {
        outcomeEnd(res, "fail", "timed out waiting for navigation")
        return res
      }

      const readyState = await ctx.browser.executeScript(`return document.readyState`)
      if (!(readyState === "complete" || readyState === "interactive")) {
        continue
      }

      // if the page has just navigated, the test run id will be reset
      // so we know we've navigated if the test run id is empty
      const state = await ctx.browser.executeScript(`return window.TESTABULOUS_testRunId`)
      if (!state) {
        break
      }
    }

    await ctx.browser.executeScript(`window.TESTABULOUS_testRunId = "${ctx.testRunId}"`)

    outcomeEnd(res, "pass")
  } catch (e) {
    console.log(chalk.redBright(`ü§ï ${e}`))
    outcomeEnd(res, "error", e!.toString())
  }
  return res
})

testRunnerCalls.set("check_if_element_is_empty", async (ctx: TestContext, args: TestContextArguments) => {
  if (!args.identifier) {
    throw new Error(`missing argument: identifier`)
  }
  if (!args.identifierType) {
    throw new Error(`missing argument: identifierType`)
  }

  const res = await executeWithTimeout(async () => {
    const res: TestStepRun = { ...outcomeStart(ctx.currentStep!.clause), stepId: ctx.currentStep!.id }

    try {
      const el = await findElement(ctx, args.identifierType, args.identifier, ctx.timeout)
      if (!el) {
        return
      }

      const children = await el.findElements({ css: "*" })
      if (children.length === 0) {
        outcomeEnd(res, "pass")
      } else {
        outcomeEnd(res, "fail", `element not empty: found=${children.length} children`)
      }

      return res
    } catch (e) {
      console.log(chalk.redBright(`ü§ï ${e}`))
      outcomeEnd(res, "error", e!.toString())
    }
  }, ctx.timeout)

  return res
})
