import fs from "fs/promises"
import path from "path"
import chalk from "chalk"
import untildify from "untildify"
import { z } from "zod"
import { Definition } from "./lib/types"

export const BrowserTypeEnum = z.enum(["chrome", "MicrosoftEdge", "firefox", "internet explorer", "safari"])
export type BrowserTypeEnum = z.infer<typeof BrowserTypeEnum>

const ConfigFileSchema = z.object({
  projectId: z.string().optional(),
  apiKey: z.string().optional(),
  baseDir: z.string().optional().default("testabulous"),
  uiPort: z.number().optional().default(4000),
  apiBaseURL: z.string().url().optional().default("https://testabulous.com/api/v1"),
  screenshots: z.boolean().optional().default(false),
  uploadResults: z.boolean().optional().default(false),
  browser: z
    .object({
      browser: BrowserTypeEnum.optional().default("chrome"),
      headless: z.boolean().optional().default(false),
      windowSize: z
        .object({
          width: z.number().optional().default(1920),
          height: z.number().optional().default(1080),
        })
        .optional(),
    })
    .optional()
    .default({
      browser: "chrome",
      headless: true,
      windowSize: { width: 1920, height: 1080 },
    }),
  envFile: z.string().optional(),
})

type ConfigFile = z.infer<typeof ConfigFileSchema>

export interface WindowSize {
  width: number
  height: number
}

export interface BrowserConfig {
  browser?: BrowserTypeEnum
  headless?: boolean
  windowSize?: WindowSize
}

export interface RuntimeConfig extends ConfigFile {
  projectId: string
  taskId?: string
  testScriptDataId?: string
  apiKey: string
  rootDir: string
  testsDir: string
  cacheDir: string
  logsDir: string
  scriptsDir: string
  screenshotsDir: string
  screenshots: boolean
  uploadResults: boolean
  file?: string // Specific test file to cache or execute, from command line
  test?: string
  force: boolean
  debug: boolean
  procedures: Definition[]
}

const defaultConfig = {
  rootDir: process.cwd(),
  baseDir: "testabulous",
  testsDir: "./tests",
  cacheDir: "./cache",
  logsDir: "./logs",
  scriptsDir: "./scripts",
  screenshotsDir: "./screenshots",
  force: false,
  debug: false,
  procedures: [],
}

/* eslint-disable @typescript-eslint/no-explicit-any */
export async function BuildConfig(opts: any, localOpts: any) {
  let c = { ...defaultConfig, ...ConfigFileSchema.parse({}), ...localOpts } as RuntimeConfig

  if (opts.rootDir && opts.configFile) {
    throw new Error("Cannot specify both --root-dir and --config-file")
  }

  let rootDir = c.rootDir
  let configFile = "testabulous.config.json"
  if (opts.rootDir) {
    rootDir = untildify(opts.rootDir)
    configFile = path.join(rootDir, "testabulous.config.json")
  } else if (opts.configFile) {
    rootDir = path.resolve(process.cwd(), path.dirname(untildify(opts.configFile)))
    configFile = path.resolve(process.cwd(), untildify(opts.configFile))
  }
  c.rootDir = path.isAbsolute(rootDir) ? rootDir : path.resolve(process.cwd(), rootDir)

  console.log(chalk.greenBright(`Using root directory ${c.rootDir}`))

  const configFileExists = await fileExists(configFile)
  if (configFileExists) {
    try {
      const data = await fs.readFile(configFile, { encoding: "utf-8" })
      const localConfig = ConfigFileSchema.parse(JSON.parse(data))
      c = { ...c, ...localConfig }
      console.log(chalk.greenBright(`üöÄ Loaded config file ${configFile}`))
    } catch (e) {
      throw `Config file ${opts.configFile} is not parsable: ${e}`
    }
  } else {
    console.log(chalk.yellow(`ü§ï Configuration file not found, using defaults`))
  }

  if (opts.envFile) {
    c.envFile = opts.envFile
  }

  if (c.envFile) {
    c.envFile = path.resolve(c.rootDir, c.envFile)
    const envFileExists = await fileExists(c.envFile)
    if (envFileExists) {
      await loadEnvFile(c.envFile)
      console.log(chalk.greenBright(`üöÄ Loaded environment file ${c.envFile}`))
    } else {
      throw new Error(`ü§ï Environment file ${c.envFile} not found`)
    }
  }

  c.apiBaseURL = process.env.TESTABULOUS_API_BASE_URL || c.apiBaseURL
  c.projectId = process.env.TESTABULOUS_PROJECT_ID || c.projectId
  c.taskId = process.env.TESTABULOUS_TASK_ID
  c.apiKey = process.env.TESTABULOUS_API_KEY || c.apiKey
  c.testScriptDataId = process.env.TESTABULOUS_TEST_SCRIPT_DATA_ID
  if (process.env.TESTABULOUS_UPLOAD_RESULTS) {
    c.uploadResults = (process.env.TESTABULOUS_UPLOAD_RESULTS || "false").toLowerCase() === "true"
  }

  if (opts.projectId) {
    c.projectId = opts.projectId
  }

  if (opts.apiKey) {
    c.apiKey = opts.apiKey
  }

  if (opts.port) {
    c.uiPort = opts.port
  }

  if (c.projectId === "") {
    throw "A Project ID is required"
  }
  if (c.apiKey === "") {
    throw "An API key is required"
  }

  c.testsDir = path.resolve(c.rootDir, c.baseDir, c.testsDir)
  c.cacheDir = path.resolve(c.rootDir, c.baseDir, c.cacheDir)
  c.logsDir = path.resolve(c.rootDir, c.baseDir, c.logsDir)
  c.scriptsDir = path.resolve(c.rootDir, c.baseDir, c.scriptsDir)
  c.screenshotsDir = path.resolve(c.rootDir, c.baseDir, c.screenshotsDir)

  await fs.mkdir(c.rootDir, { recursive: true })
  await fs.mkdir(c.testsDir, { recursive: true })
  await fs.mkdir(c.cacheDir, { recursive: true })
  await fs.mkdir(c.logsDir, { recursive: true })
  await fs.mkdir(c.scriptsDir, { recursive: true })
  await fs.mkdir(c.screenshotsDir, { recursive: true })

  return c
}

async function loadEnvFile(file: string) {
  try {
    const data = await fs.readFile(file, { encoding: "utf-8" })
    data.split("\n").forEach((line) => {
      line = line.trim()
      if (line.length === 0) return
      if (line.startsWith("#")) return
      if (line.startsWith("//")) return
      const ix = line.indexOf("#")
      if (ix > -1) {
        line = line.substring(0, ix).trim()
      }
      const [key, value] = line.split("=")
      if (key && value) {
        process.env[key] = value
      } else {
        throw new Error(`ü§ï Could not parse environment file ${file}: malformed (${line})`)
      }
    })
  } catch (e) {
    throw new Error(`ü§ï Could not parse environment file ${file}`)
  }
}

export async function fileExists(file: string) {
  return fs
    .stat(file)
    .then(() => true)
    .catch(() => false)
}

export async function ensureDir(path: string) {
  const exists = await fileExists(path)
  if (!exists) {
    await fs.mkdir(path, { recursive: true })
  }
}
