import "chromedriver"
import { ThenableWebDriver, Builder, logging } from "selenium-webdriver"
import * as chrome from "selenium-webdriver/chrome.js"
import chalk from "chalk"
import { RuntimeConfig, BrowserTypeEnum } from "../config"

let browser: ThenableWebDriver | null = null

export async function createBrowser(cfg: RuntimeConfig) {
  if (browser) {
    return browser
  }

  console.log(chalk.greenBright(`‚è≥ Starting web driver`))

  const browserType = cfg.browser?.browser ?? BrowserTypeEnum.Values.chrome
  const size = {
    width: cfg.browser?.windowSize?.width ?? 1920,
    height: cfg.browser?.windowSize?.height ?? 1080,
  }
  const headless = cfg.browser?.headless ?? false
  const disableGpu = cfg.browser?.headless ?? false

  const loggingPrefs = new logging.Preferences()
  loggingPrefs.setLevel(logging.Type.BROWSER, logging.Level.ALL)

  let chromeOptions = new chrome.Options()
  if (headless) {
    chromeOptions = chromeOptions.addArguments("--headless=new")
  }
  if (disableGpu) {
    chromeOptions = chromeOptions.addArguments("--disable-gpu")
  }
  chromeOptions = chromeOptions.addArguments("--no-sandbox")
  chromeOptions = chromeOptions.addArguments("--disable-dev-shm-usage")

  chromeOptions = chromeOptions.windowSize(size).setLoggingPrefs(loggingPrefs) as chrome.Options

  console.log(chalk.blue(`Staring browser: ${browser}`))
  console.log(chalk.blue(`Screen size: ${size.width}x${size.height}`))
  console.log(chalk.blue(`Headless: ${headless}`))
  browser = new Builder().forBrowser(browserType).setChromeOptions(chromeOptions).build()

  return browser
}

export async function closeBrowser() {
  if (browser) {
    await browser.quit()
  }
  browser = null
}
